<template>
  <div class="edit-container">
    <el-form
      :model="formData"
      ref="formData"
      label-width="140px"
      class="edit-form"
      :rules="rules"
    >
      <el-form-item label="/" prop="address1">
        <el-cascader
          class="form-box-input"
          filterable
          v-model="formData.address1"
          :options="cityData"
        ></el-cascader>
      </el-form-item>
      <el-form-item label="" prop="address2">
        <el-input v-model="formData.address2" class="form-box-input"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          @click="
            {
              $emit('close')
              resetForm('formData')
            }
          "
          ></el-button
        >
        <el-button type="primary" @click="submitForm('formData')"
          ></el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import cityData from '@/utils/citydata.js'

const INITDATA = {
  address1: '',
  address2: ''
}
export default {
  props: {
    model: Object
  },
  watch: {
    model: {
      handler(val) {
        this.formData = { ...INITDATA, ...val }
      },
      immediate: true
    }
  },
  data() {
    return {
      formData: {},
      cityData,
      rules: {
        address1: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$message.error('')
          this.$emit('editEmit')
        }
      })
    }
  }
}
</script>

<style lang="less" scoped></style>
